import React, { Component } from 'react';
import { Route, Switch, withRouter, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import asyncComponent from './hoc/asyncComponent/asyncComponent';


import Layout from './hoc/Layout/Layout';
import BurgerBuilder from './containers/BurgerBuilder/BurgerBuilder';
// import Checkout from './containers/Checkout/Checkout'; NOT NEEDED WITH LAZY LOADING
// import Orders from './containers/Orders/Orders'; NOT NEEDED WITH LAZY LOADING
//import Auth from './containers/Auth/Auth'; NOT NEEDED WITH LAZY LOADING
import Logout from './containers/Auth/Logout/Logout';
import * as actions from './store/actions/index';


const asyncCheckout = asyncComponent(() => {
  return import('./containers/Checkout/Checkout');
});

const asyncOrders = asyncComponent(() => {
  return import('./containers/Orders/Orders');
});

const asyncAuth = asyncComponent(() => {
  return import('./containers/Auth/Auth');
});





class App extends Component {

  componentDidMount () {
    this.props.onTryAutoSignup();
  }

  render() {

    let routes = (
          <Switch>
            <Route path="/auth" exact component={asyncAuth} />
            <Route path="/" exact component={BurgerBuilder} />
            <Redirect to ="/"/>
          </Switch>
    );

    if (this.props.isAuthenticaed) {
      routes = (
          <Switch>
            <Route path="/checkout" component={asyncCheckout} />
            <Route path="/orders" component={asyncOrders} />
            <Route path="/logout" exact component={Logout} />
            <Route path="/auth" exact component={asyncAuth} />
            <Route path="/" exact component={BurgerBuilder} />
            <Redirect to ="/"/>
          </Switch>
      );
    };


    return (
      <div>

        <Layout>
          {routes}
        </Layout>

      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    isAuthenticaed: state.auth.token !== null
  }
}

const mapDispatchToProps = dispatch => {
  return {
    onTryAutoSignup: () => dispatch(actions.authCheckState())
  }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));



